import React, { useState } from 'react';
import './Register.css';
import Header from '../components/header/header';
import Footer from '../components/footer/footer';

const Register = () => {
  const [formData, setFormData] = useState({
    fullName: '',
    gender: '',
    birthDate: '',
    birthPlace: '',
    address: '',
    phone: '',
    email: '',
    facebook: '',
    school: '',
    course: [],
    studyTime: '',
    testDate: '',
    startDate: '',
    referral: '',
  });

  const [isSubmitting, setIsSubmitting] = useState(false);
  const [error, setError] = useState('');

  const handleChange = (e) => {
    const { name, value, type, checked } = e.target;
    if (type === 'checkbox') {
      if (name === 'course') {
        setFormData({
          ...formData,
          [name]: checked
            ? [...formData[name], value]
            : formData[name].filter((item) => item !== value),
        });
      } else {
        setFormData({ ...formData, [name]: checked });
      }
    } else {
      setFormData({ ...formData, [name]: value });
    }
  };

  const handleSubmit = async () => {
    setIsSubmitting(true);
    setError('');

    try {
      const response = await fetch('https://676cffa90e299dd2ddfe11f1.mockapi.io/message', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(formData),
      });

      if (!response.ok) {
        throw new Error(' c li xy ra khi gi d liu');
      }

      const result = await response.json();
      alert('ng k thnh cng!');
      console.log(result);
    } catch (err) {
      setError('C li xy ra khi gi d liu. Vui lng th li.');
      console.error(err);
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <div className="register-register-container">
      <div>  <Header/></div>
      <form onSubmit={(e) => e.preventDefault()} className="register-form">
        <h2 className="register-h2">Thng tin ng k kha hc</h2>
        <div className="register-register-form-group-inline">
          <div className="register-register-form-group">
            <label>H tn: </label>
            <input
              type="text"
              name="fullName"
              value={formData.fullName}
              onChange={handleChange}
            />
          </div>
          <div className="register-register-form-group">
            <label>Gii tnh: </label>
            <input
              type="radio"
              name="gender"
              value="Nam"
              checked={formData.gender === 'Nam'}
              onChange={handleChange}
            /> Nam
            <input
              type="radio"
              name="gender"
              value="N"
              checked={formData.gender === 'N'}
              onChange={handleChange}
            /> N
          </div>
        </div>

        <div className="register-register-form-group-inline">
          <div className="register-register-form-group">
            <label>Ni sinh: </label>
            <input
              type="text"
              name="birthPlace"
              value={formData.birthPlace}
              onChange={handleChange}
            />
          </div>
          <div className="register-register-form-group">
            <label>Ngy sinh: </label>
            <input
              type="date"
              name="birthDate"
              value={formData.birthDate}
              onChange={handleChange}
            />
          </div>
        </div>

        <div className="register-register-form-group">
          <label>a ch lin lc: </label>
          <input
            type="text"
            name="address"
            value={formData.address}
            onChange={handleChange}
          />
        </div>

        <div className="register-register-form-group-inline">
          <div className="register-register-form-group">
            <label>Email: </label>
            <input
              type="email"
              name="email"
              value={formData.email}
              onChange={handleChange}
            />
          </div>
          <div className="register-register-form-group">
            <label>Facebook: </label>
            <input
              type="text"
              name="facebook"
              value={formData.facebook}
              onChange={handleChange}
            />
          </div>
        </div>

        <div className="register-register-form-group">
          <label>Tn trng/ngnh ngh: </label>
          <input
            type="text"
            name="school"
            value={formData.school}
            onChange={handleChange}
          />
        </div>

        <div className="register-register-form-group register-register-checkbox-group">
          <label>Kha hc mun ng k: </label>
          <input
            type="checkbox"
            name="course"
            value="Ting Anh Cn Bn"
            checked={formData.course.includes('Ting Anh Cn Bn')}
            onChange={handleChange}
          /> Ting Anh Cn Bn
          <input
            type="checkbox"
            name="course"
            value="IELTS"
            checked={formData.course.includes('IELTS')}
            onChange={handleChange}
          /> IELTS
        </div>

        <div className="register-register-form-group">
          <label>Thi gian hc: </label>
          <select
            name="studyTime"
            value={formData.studyTime}
            onChange={handleChange}
          >
            <option value="">Chn thi gian</option>
            <option value="Sng">Sng</option>
            <option value="Chiu">Chiu</option>
            <option value="Ti">Ti</option>
          </select>
        </div>

        <div className="register-register-form-group">
          <label>Ngy lm bi test: </label>
          <input
            type="date"
            name="testDate"
            value={formData.testDate}
            onChange={handleChange}
          />
        </div>

        <div className="register-register-form-group">
          <label>Ngy bt u kha hc: </label>
          <input
            type="date"
            name="startDate"
            value={formData.startDate}
            onChange={handleChange}
          />
        </div>

        <div className="register-register-form-group">
          <label>Bit n trung tm t u? </label>
          <input
            type="text"
            name="referral"
            value={formData.referral}
            onChange={handleChange}
          />
        </div>

        {error && <div className="register-error-message">{error}</div>}
        <button
          type="button"
          onClick={handleSubmit}
          className="register-button"
          disabled={isSubmitting}
        >
          {isSubmitting ? 'ang x l...' : 'Submit'}
        </button>
      </form>
      <div><Footer className="register-footer"/></div>
    </div>
  );
};

export default Register;