import React, { useState, useEffect } from 'react';
import ChatIcon from '@mui/icons-material/Chat'; 
import CloseIcon from '@mui/icons-material/Close'; 
import '../ChatBot/ChatBot.css';

const ChatBox = () => {
    const [messages, setMessages] = useState([]);
    const [isVisible, setIsVisible] = useState(true); 

    const defaultMessages = [
        { text: "Hi there! How can I assist you today?", isUser: false },
        { text: "You can ask me about courses, registration, or anything else.", isUser: false },
    ];

    useEffect(() => {
        // Hin th tin nhn mc nh sau 1 giy khi ChatBot c m
        const timer = setTimeout(() => {
            setMessages(defaultMessages);
        }, 1000); 
        return () => clearTimeout(timer);
    }, []);

    // Hm gi tin nhn n mock API
    const sendToAdminAPI = async (message) => {
        try {
            const response = await fetch('https://676cffa90e299dd2ddfe11f1.mockapi.io/message', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({ text: message, isUser: true }), // D liu gi ln API
            });

            if (!response.ok) {
                throw new Error('Khng th gi tin nhn ti admin');
            }

            const result = await response.json();
            console.log(' gi thng bo ti admin:', result); // In ra kt qu t API
        } catch (error) {
            console.error('Li gi tin nhn:', error); // X l li nu c
        }
    };

    // X l khi ngi dng gi tin nhn
    const handleSendMessage = (message) => {
        // Cp nht tin nhn mi vo trng thi messages
        setMessages(prevMessages => [...prevMessages, { text: message, isUser: true }]);
        
        // Gi tin nhn  ln mock API
        sendToAdminAPI(message);
    };

    // Hm ng/m ChatBox
    const toggleChatBox = () => {
        setIsVisible(!isVisible);
    };

    // M ChatBox khi ngi dng nhn vo icon
    const openChatBox = () => {
        setIsVisible(true);
    };

    return (
        <>
            {!isVisible && (
                <div className="chatBotIcon" onClick={openChatBox}>
                    <ChatIcon fontSize="large" />
                </div>
            )}

            {isVisible && (
                <div className="chatBox">
                    <div className="chatWindow">
                        {messages.map((message, index) => (
                            <div key={index} className={message.isUser ? 'userMessage' : 'aiMessage'}>
                                <p>{message.text}</p>
                            </div>
                        ))}
                    </div>
                    <div className="chatInput">
                        <input 
                            type="text" 
                            placeholder="Type your message..." 
                            onKeyDown={(e) => e.key === 'Enter' && handleSendMessage(e.target.value)}
                        />
                    </div>
                    <div className="closeIcon" onClick={toggleChatBox}>
                        <CloseIcon />
                    </div>
                </div>
            )}
        </>
    );
};

export default ChatBox;